(function ( wpI18n, wpHooks, wpBlockEditor, wpComponents, wpElement ) {
    wpBlockEditor = wp.blockEditor || wp.editor;
    const { addFilter } = wpHooks;
    const { __ } = wpI18n;
    const { InspectorControls } = wpBlockEditor;
    const { PanelBody, RangeControl } = wpComponents;
    const { Fragment } = wpElement;

    // Set attributes
    addFilter( 'blocks.registerBlockType', 'advgb/registerAdvTabsProClass', function ( settings ) {
        if ( settings.name === 'advgb/adv-tabs' ) {
            settings.attributes = Object.assign( settings.attributes, {
                fontSize: {
                    type: 'number'
                }
            } );
        }

        return settings;
    } );

    // Add fields to sidebar
    addFilter( 'editor.BlockEdit', 'advgb/advTabsPro', function ( BlockEdit ) {
        return ( props ) => {
            return (
                <Fragment>
                    <BlockEdit {...props} />
                    { props.isSelected && props.name === 'advgb/adv-tabs' && (
                    <Fragment>
                        <InspectorControls>
                            <PanelBody title={ __('Tab Settings', 'advanced-gutenberg') } className="advgb-pro-icon">
                                <RangeControl
                                    label={ __( 'Font Size', 'advanced-gutenberg' ) }
                                    value={ props.attributes.fontSize || '' }
                                    onChange={ ( value ) => props.setAttributes( { fontSize: value } ) }
                                    min={ 10 }
                                    max={ 100 }
                                    beforeIcon="editor-textcolor"
                                    allowReset
                                />
                            </PanelBody>
                        </InspectorControls>
                    </Fragment>
                    ) }
                    { props.name === 'advgb/adv-tabs' && props.attributes.pid && (
                        <div>
                            <style>
                            { `#block-${props.clientId} ul.advgb-tabs-panel li.advgb-tab:not(.advgb-add-tab) {
                                font-size: ${props.attributes.fontSize}px;
                            }` }
                            </style>
                        </div>
                    ) }
                </Fragment>
            )
        }
    } );

})( wp.i18n, wp.hooks, wp.blockEditor, wp.components, wp.element );
